<template>
  <div class="row tracking">
    <div class="col-12">
      <button v-if="timeState === 1" type="button" class="btn btn-lg btn-danger" @click="timeTracking" :key="timeState">
        <i class="fas fa-stop"></i><br>Stop
      </button>
      <button v-else type="button" class="btn btn-lg btn-warning" @click="timeTracking" :key="timeState"><i
          class="fas fa-play"></i><br>Start
      </button>
    </div>
    <div class="col-12">
      Arbeitszeit:{{ duration }}
    </div>
  </div>
</template>

<script>
import axios from 'axios';
import dayjs from 'dayjs';
import duration from 'dayjs/plugin/duration';

dayjs.extend(duration);

export default {
  name: "TimeButtons",
  props: {
    userId: {
      type: Number,
      required: true
    }
  },
  data() {
    return {
      workingTime: 0,
      breakTime: 0,
      timeState: 0,
      loggedInUser: this.userId,
      duration: '00:00:00',
      timer: null
    }
  },
  watch: {
    userId(newValue) {
      console.log('UserId updated');
      localStorage.loggedInUser = newValue;
    }
  },
  created() {



  },
  mounted() {
    if (localStorage.workingTime) {
      console.log('Working-Time restored');
      this.workingTime = localStorage.workingTime;
      this.duration = dayjs.unix(this.workingTime).format('HH:mm:ss');
    }
    if (localStorage.breakTime) {
      console.log('Break-Time restored');
      this.breakTime = localStorage.breakTime;
    }
    if (localStorage.timeState) {
      console.log('Working-Time-State restored');
      this.timeState = parseInt(localStorage.timeState);
    }
    if (localStorage.loggedInUser) {
      console.log('UserId restored');
      this.loggedInUser = localStorage.loggedInUser;
    }
  },
  methods: {
    timeTracking() {
      axios.post(
          'http://localhost/Galle/pwa/toggleTimeTrack',
          {userId: this.loggedInUser},
      ).then((response) => {
        console.log(response.data);
        localStorage.workingTime = this.workingTime = response.data.workingTime;
        localStorage.breakTime = this.breakTime = response.data.breakTime;
        this.duration = dayjs.unix(this.workingTime).format('HH:mm:ss');
        if (this.timeState === 0) {
          localStorage.timeState = this.timeState = 1;
          this.startTimer();
        } else {
          localStorage.timeState = this.timeState = 0;
          this.stopTimer();
        }
      }).catch((error) => {
        console.log('ERROR-TimeButton');
        console.log(error);
        this.errLoginCheck = true;
      });
    },
    startTimer() {
      if (this.timer === null) {
        this.timer = setInterval(() => {
          this.workingTime += 1;
          this.duration = dayjs.unix(this.workingTime).format('HH:mm:ss');
        },1000);
      }
    },
    stopTimer() {
      clearInterval(this.timer);
      this.timer = null;
    }
  }

}
</script>

<style scoped>
.tracking button {
  height: 10rem;
  width: 10rem;
}


</style>